.setting-container {
    display: flex;
    margin-bottom: 5px;
    box-sizing: border-box;
    border-radius: 5px;
    padding-left: 10px;
    padding-right: 10px;
    width: 300px;
    height: 50px;
    background-color: #58595b;
    align-items: center;
    justify-content: space-between;
}

.setting-container:last-child {
    margin-bottom: 0;
}

.setting-subtitle {
    color: #f3f3f3;
}

.btn-switch {
    display: flex;
    box-sizing: border-box;
    border-radius: 28px;
    padding-left: 5px;
    padding-right: 5px;
    width: 45px;
    height: 28px;
    background-color: #303235;
    align-items: center;
    transition: .15s;
    cursor: pointer;
}

.btn-switch-on {
    background-color: #368db9;
}

.switch-slider {
    border-radius: 100%;
    box-shadow: 0 0 4px 2px rgba(0, 0, 0, 0.25);
    width: 20px;
    height: 20px;
    background-color: #d8d8d8;
    transition: .15s;
}

.switch-slider-on {
    margin-left: 15px;
    background-color: #f3f3f3;
}

.btn-switch-block {
    background-color: rgba(54, 141, 185, 0.5);
    cursor: default;
}

.switch-slider-block {
    background-color: rgba(243, 243, 243, 0.5);
}

@media (max-width: 420px) {
    .setting-container {
        width: 100%;
    }
}